<template >
  <div id="dashboard">
    <nav>
      <h4 class="titulo">EmployeesCRUD</h4>
    </nav>
  <div class="container">
    <ul class="colletion with-header">
      <li class="colletion-header"><h4>Employees</h4></li>
      <li v-for="employee in employees" v-bind:key="employee.id"
      class="colletion-item">
        <div class="chip">{{employee.dept}}</div>{{employee.employee_id}}:{{employee.name}}
        <router-link class="secondary-content"
        v-bind:to="{name:'view-employee', params: {employee_id: employee.employee_id}}">
        <i class="fa fa-eye"></i>
        </router-link>
      </li>
    </ul>

      <div class="fixed-action-btn">
        <router-link to="/new"class="btn-floating btn-large red">
          <i class="fa fa-plus"> </i>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name:'dashboard',
  data(){
    return {
      employees:[]
    }
  },
  created () {
    db.collection('employees').orderBy('dept').get().then
    (querySnapshot => {
      querySnapshot.forEach(doc => {
        const data = {
          'id': doc.id,
          'employee_id': doc.data().employee_id,
          'name': doc.data().name,
          'dept': doc.data().dept,
          'position': doc.data().position
        }
        this.employees.push(data)
      })
    })
  }
}
</script>



